.container {
	max-height: calc(100vh - 288px - 56px);
	display: flex;
	flex-direction: column;
	gap: 16px;
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.content {
	padding-left: 32px;
}

.scroll {
	display: flex;
	flex-direction: column;
	row-gap: 16px;
	max-height: 80%;
	width: 100%;
	overflow-y: scroll;
}